import React,{useState, useEffect} from 'react'

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { io } from "socket.io-client";

import "./ChatContainer.css"
import { useContext } from 'react';
import { CartContext } from '../../context/CartContext';

const ChatContainer = () => {

  const [message, setMessage] = useState([]);
  const [messages, setMessages] = useState([]);
  
  
  const {perfil} = useContext(CartContext)
  const handleChange=(e)=>{    
    setMessage(
          {
              ...message,
              [e.target.name]: e.target.value
          }    
      )        
  }
  const socket = io();

  const enviarMensaje = async (e) => {    
    const datos = {
      email: perfil.email,
      tipo: "usuario",
      mensaje : message.message
    }
    setMessage({message: ""})
    socket.emit("envio", datos);
  }

  useEffect(() => {
    
    socket.on('connection')    
   socket.on('mensajes', (data)=>{    
    setMessages(data)        
  })
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div>
      {
        messages ?
        <div className='contenedorMsg'>
          {messages.map((e, index) =>{
            if (e.email === perfil.email) {
              return <>
                <li key={index} className='derecha nombre'>{e.email}</li> 
                <li key={e.date} className='derecha derechaMsg'>{e.mensaje}</li></>
            }else{
              return <>
              <li key={index} className='izquierda nombre'>{e.email}</li> 
              <li key={e.date} className='izquierda izquierdaMsg'>{e.mensaje}</li></>              
            }
            }) 
          }
        </div> 
        :
        <h1>Sin mensajes</h1>
      }
      <form onChange={handleChange} >
        <div>          
            <TextField
                type="text" name="message" value={message.message}
                id="outlined-multiline-static"
                rows={4}
                label="Mensaje"                
            />
        </div>        
        <Button variant="contained" size="small" onClick={async (e) =>{
            await enviarMensaje(e)                   
        }}>                            
                Enviar
            </Button>
    </form>
    </div>
  )
}

export default ChatContainer