import {useContext} from 'react'
import {CartContext} from '../../context/CartContext.jsx'
import {Link} from 'react-router-dom'
import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/Delete';

import './Cart.css'

const Cart = () => {

    const {eliminarCarrito, eliminarItem, generarOrden, prodsEnCarrito} = useContext(CartContext)    

    const precioTotal = () =>{
        let total = 0
    
        prodsEnCarrito.forEach((value) =>{            
            total += value.cantidad * value.price
        })
        total = total.toFixed(2);
        return total
    }

    const confirmar = () => {
        generarOrden()
    }

    return (
        <div>
            {prodsEnCarrito.length ?
                <>
                    {prodsEnCarrito && prodsEnCarrito.map(item => 
                        <article key={item.id} className="item">
                            <h2>{item.title}</h2>
                            <span><b>Precio:</b>{` $${Number(item.cantidad * item.price).toFixed(2)}`}</span>                            
                            <span><b>Cantidad:</b>{` ${item.cantidad}`}</span>
                            <Button onClick={() => eliminarItem(item.id)} variant="outlined" startIcon={<DeleteIcon />}>
                                Borrar
                            </Button>
                        </article>
                        )}
                        
                        <h1>Total: ${precioTotal()}</h1>
                        <Button onClick={eliminarCarrito} variant="outlined" color="error">
                            Limpiar carrito
                        </Button>
                        <Button onClick={confirmar} variant="contained" color="success">
                            Finalizar compra
                        </Button>                        
                </>
            :
               <>
                    <h1>Carrito vacio</h1>
                    <Link to={`/`}>
                        <Button variant="outlined">Home</Button>
                    </Link>
               </>
            }
        </div>
    )
}

export default Cart