import React, { useEffect, useState } from 'react'
import axios from 'axios'

import {useContext} from 'react'
import {CartContext} from '../../context/CartContext.jsx'

const Perfil = () => {

    const [user, setUser] = useState({})
    const [loading, setLoading] = useState(true)

    const {token} = useContext(CartContext)

    useEffect(() => {
        axios({
	
            method: 'GET',
            url: `/perfil`,
            headers:{
                Authorization: token
            }
            })
            .then(response => {                
                setUser(response.data)
                setLoading(false)
                
            })
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])
    
  return (
    <div>
        {loading ? <h1 className='margenTop'>Cargando</h1> : 
        <ul>
            <li style={{ listStyleType: "none" }}>Email: {user.email}</li>
            <li style={{ listStyleType: "none" }}>Nombre: {user.nombre}</li>
            <li style={{ listStyleType: "none" }}>Direccion: {user.direccion}</li>
            <li style={{ listStyleType: "none" }}>Telefono: {user.telefono}</li>    
            <li style={{ listStyleType: "none" }}>Edad: {user.edad}</li>
            <li style={{ listStyleType: "none" }}><img src={user.avatar} alt="avatar" style={{ width: "100px "}} /></li>
        </ul>
        }
    </div>
  )
}

export default Perfil