import React, {useState} from 'react'
import { Redirect } from 'react-router-dom';
import axios from 'axios'

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { useContext } from 'react';
import { CartContext } from '../../context/CartContext';


const AddItem = () => {
    const [datos, setDatos] = useState({
        title:"",
        price:"",
        description: "",
        image: "",
        count: "",
        category: ""
        }
    )
    const {token} = useContext(CartContext)
    
    const [redirect, setRedirect] = useState(false)
    
    const handleChange=(e)=>{        
        setDatos(
            {
                ...datos,
                [e.target.name]: e.target.value
            }    
        )        
    }

    const agregar = async (e) =>{
        e.preventDefault()
        for (var key in datos) {            
            if (datos[key] === null || datos[key] === "" || datos[key] === " "){
                alert("Hay campos vacios")
                return;        
            }
        }
        
        await axios({
	
            method: 'POST',
            url: `/productos/`,
            headers:{
                Authorization: token
            },
            data: {
                title: datos.title,
                price: datos.price,
                description: datos.description,                
                image: datos.image,
                count: datos.count,
                category: datos.category
            }})
            .then(resp => resp.data)            
        
    }

    const renderRedirect = () => {
        
        if (redirect===true) {
            return <Redirect to='/' />
        }
    }

  return (
    <>
    {renderRedirect()}
    <form onSubmit={async () =>{        
            await agregar()
            setRedirect(true)
        }} onChange={handleChange} >
        <div>            
            <TextField
                type="text" name="title" defaultValue={""}
                sx={{ m: 1, width: '25ch' }}
                id="outlined-required"
                label="Titulo"
            />            
        </div>
        <div>
            <TextField
                type="text" name="price" defaultValue={""}
                id="outlined-required"
                label="Precio"                
            />            
        </div>
        <div>
            <TextField
                type="text" name="description" defaultValue={""}
                id="outlined-multiline-static"
                rows={4}
                label="Descripcion"                
            />
        </div>
        <div>
            <TextField
                type="text" name="image" defaultValue={""}
                id="outlined-required"
                label="Imagen"                
            />
        </div>
        <div>
            <TextField
                type="text" name="count" defaultValue={""}
                id="outlined-required"
                label="Stock"                
            />
        </div>
        <div>
            <FormControl>
                <InputLabel id="demo-simple-select-label">Categoria</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        name='category'
                        value={datos.category}
                        sx={{ m: 1, width: '25ch' }}
                        label="Categoria"
                        onChange={handleChange}
                    >                        
                    <MenuItem value={"women's clothing"}>women's clothing</MenuItem>
                    <MenuItem value={"men's clothing"}>men's clothing</MenuItem>
                    <MenuItem value={"electronics"}>electronics</MenuItem>
                    <MenuItem value={"jewelery"}>jewelery</MenuItem>
                </Select>
            </FormControl>            
        </div>
        <Button onClick={async (e) =>{
            await agregar(e)
            setRedirect(true)
        }} variant="contained" color="success">
            Agregar
        </Button>                
    </form>
    </>
  )
}

export default AddItem