import React, {useState, useContext} from 'react'
import {Link} from 'react-router-dom'
import {CartContext} from '../../context/CartContext.jsx'
import axios from 'axios'

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import ErrorHandler from '../ErrorHandler/ErrorHandler.jsx'

const Login = ({setTokenInicio}) => {
    const {setToken, getPerfil} = useContext(CartContext)

    const [loginFail, setLoginFail] = useState(null)


    const [datos, setDatos] = useState({
        email:"",
        password:"",
        }
    )
    
    const handleChange=(e)=>{
        setDatos(
            {
                ...datos,
                [e.target.name]: e.target.value
            }    
        )        
    }

    const login = async (e) =>{
        e.preventDefault()
        for (var key in datos) {
            if (datos[key] === null || datos[key] === "" || datos[key] === " "){
                alert("Hay campos vacios")
                return;        
            }
        }
        
        await axios({
	
            method: 'POST',
            url: `/login`,
            data: {
                email: datos.email,
                password: datos.password,                
            }})
            .then(resp => resp.data)
            .then(data => {                
                if (data.error) {
                    setLoginFail(data.error)
                }else{
                    setToken(data)
                    getPerfil(data)
                    setTokenInicio(data)
                }
            })        
    }

  return (
    <>
       {loginFail ? <ErrorHandler setLoginFail={setLoginFail} error={loginFail}/> :
    <>  
    <h1 className='margenTop'>Login</h1>
    <form onChange={handleChange} >
        <div>

            <TextField
                type="text" name="email" defaultValue={""}
                id="outlined-multiline-static"
                rows={4}
                label="Email"                
            />
        </div>
        <div>

            <TextField
                type="text" name="password" defaultValue={""}
                id="outlined-required"
                label="Contraseña"
            />            
        </div>
        <Button variant="contained" size="small" onClick={async (e) =>{
            await login(e)
        }}>                            
                Login
            </Button>
        <Link to={`/register`}>
            <Button variant="contained" size="small">                            
                Registrar
            </Button>
        </Link> 
    </form>
    </>
    }</>
  )
}

export default Login