import React from 'react'
import Item from '../Item/Item'
import { Grid } from '@material-ui/core'
import { memo } from 'react'


const ItemList = memo(
    ({items}) => {

    return (
        <div>
            {items.length ?
                <Grid
                    container
                    spacing={0}
                    direction="row"
                    alignItems="flex-start"
                    justifyContent="space-evenly"                
                    >
                        {items && items.map((prod) => <Item key={prod.id} id={prod.id} title={prod.title} count={prod.count} description={prod.description} price={prod.price} image={prod.image} category={prod.category}initial={1}/>)}
                </Grid>
            : <h1>Cargando...</h1>}
        </div>
    )
}
, (oldProps, newProps)=> oldProps.items.length === newProps.items.length)

export default ItemList