import React, {useState} from 'react'
import axios from 'axios'
import { Redirect } from 'react-router-dom';

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { CartContext } from '../../context/CartContext';
import { useContext } from 'react';

const Edit = ({id, title, price, description, image, count, category}) => {

    
    const {token} = useContext(CartContext)
    const [datos, setDatos] = useState({
        title:title,
        price:price,
        description: description,
        image: image,
        count: count,
        category: category
        }
    )

    const [redirect, setRedirect] = useState(false)
    
    const handleChange=(e)=>{        
        setDatos(
            {
                ...datos,
                [e.target.name]: e.target.value
            }    
        )        
    }

    const actualizar = async (e) =>{
        e.preventDefault()
        for (var key in datos) {            
            if (datos[key] === null || datos[key] === "" || datos[key] === " "){
                alert("Hay campos vacios")
                return;        
            }
        }        
        await axios({
	
            method: 'PUT',
            url: `/productos/${id}`,
            headers:{
                Authorization: token
            },
            data: {
                title: datos.title,
                price: datos.price,
                description: datos.description,                
                image: datos.image,
                count: datos.count,
                category: datos.category
            }})
            .then(resp => resp.data)
            .then(data => {                
            })        
    }

    const renderRedirect = () => {        
        if (redirect===true) {            
            return <Redirect to='/' />
        }       
    }

    const eliminar = () => { 
        axios({
            method: 'DELETE',
            url: `/productos/${id}`,
            headers:{
                Authorization: token
            },
            data: {
                id: id
            }})
            .then(resp => resp.data)
            .then(data => {                  
            })
    }

  return (
    <>
    {renderRedirect()}
    <form onSubmit={actualizar} onChange={handleChange} >
        <div>            
            <TextField
                type="text" name="title" defaultValue={title}
                sx={{ m: 1, width: '25ch' }}
                id="outlined-required"
                label="Titulo"
            />            
        </div>
        <div>
            <TextField
                type="text" name="price" defaultValue={price}
                id="outlined-required"
                label="Precio"                
            />            
        </div>
        <div>
            <TextField
                type="text" name="description" defaultValue={description}
                id="outlined-multiline-static"
                rows={4}
                label="Descripcion"                
            />
        </div>
        <div>
            <TextField
                type="text" name="image" defaultValue={image}
                id="outlined-required"
                label="Imagen"                
            />
        </div>
        <div>
            <TextField
                type="text" name="count" defaultValue={count}
                id="outlined-required"
                label="Stock"                
            />
        </div>
        <div>
            <TextField
                type="text" name="category" defaultValue={category}
                id="outlined-required"
                label="Categoria"                
            />
        </div>
        <Button onClick={actualizar} variant="contained" color="success">                
            Actualizar
        </Button> 
        <Button onClick={async () =>{
            await eliminar()
            setRedirect(true)
        }} variant="contained" color="success">                
            Eliminar
        </Button>        
    </form>
    </>
  )
}

export default Edit
