import {useState, useEffect} from 'react'
import { useParams } from "react-router";
import axios from 'axios'
import ItemDetail from '../ItemDetail.jsx/ItemDetail'
import { useContext } from 'react';
import { CartContext } from '../../context/CartContext';

const ItemDetailContainer = ({tokenid}) => {

    const [producto, setProducto] = useState(null)
    const [loading, setLoading] =useState(true)
    const {id} = useParams()

    const {token} = useContext(CartContext)

    const getProduct = () =>{              
        axios({
	
            method: 'GET',
            url: `/productos/${id}`,
            headers:{
                Authorization: token
            },
            })
            .then(response => response.data)
            .then(data =>setProducto(data))
            .finally(setLoading(false))
    }
    
    useEffect(() => {
        getProduct();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <div>
            {loading ? 
                <h2>Cargando...</h2> 
            : 
                producto && <ItemDetail id={producto.id} title={producto.title} price={producto.price} description={producto.description} image={producto.image} count={producto.count} category={producto.category} /> 
            }
        </div>
    )
}

export default ItemDetailContainer
