import React ,{useEffect}from 'react'
import CartWidget from '../Stateless/CartWidget'
import {Link} from 'react-router-dom'
import {useContext} from 'react'
import {CartContext} from '../../context/CartContext.jsx'

import "./NavBar.css"

const Navbar = () => {

    const categoria = ["women's clothing", "men's clothing", "electronics", "jewelery"]    

    const {crearCarrito} = useContext(CartContext)

    useEffect(() => {
        crearCarrito()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <header>
            <h1>
                <Link to="/">
                    e-commerce
                </Link>
            </h1>
            <h3>
            <Link to="/agregar">
                agregar
            </Link>
            </h3>
            <nav>
                <ul>
                    {categoria.map((nombre) => <li key={nombre}><Link to={`/category/${nombre}`}>{nombre}</Link></li>)}                    
                </ul>
            </nav>
            <Link to="/perfil">Perfil</Link>            
            <Link to="/chat">Chat</Link>            
            <Link to="/cart">
                <CartWidget/>
            </Link>            
        </header>
    )
}

export default Navbar
