import React, {useState} from 'react'
import { Link, Redirect } from 'react-router-dom';
import axios from 'axios'

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';


import 'react-phone-number-input/style.css'
import PhoneInput from 'react-phone-number-input'
import ErrorHandler from '../ErrorHandler/ErrorHandler';

const Register = () => {

    const [loginFail, setLoginFail] = useState(null)

    const [datos, setDatos] = useState({
        email:"",
        password:"",
        nombre: "",
        direccion: "",
        edad: "",
        telefono: "",
        avatar: ""
        }
    )

    const [redirect, setRedirect] = useState(false)
    const [value, setValue] = useState()
    const handleChange=(e)=>{        
        setDatos(
            {
                ...datos,
                [e.target.name]: e.target.value
            }    
        )        
    }

    const registrar = async (e) =>{
        e.preventDefault()
        for (var key in datos) {
            if (datos[key] === null || datos[key] === "" || datos[key] === " "){
                alert("Hay campos vacios")
                return;        
            }
        }

        await axios({
            method: 'POST',
            url: `/register`,
            data: {
                email: datos.email,
                password: datos.password,
                nombre: datos.nombre,
                direccion: datos.direccion,
                edad: datos.edad,
                telefono: value,
                avatar: datos.avatar
            }})
            .then(resp => resp.data)
            .then(data => {
                if (data.error) {
                    setLoginFail(data.error)
                }           
            })
    }

    const renderRedirect = () => {
        if (redirect===true) {
            return <Redirect to='/'/>
        }
       
    }
  return (
    <>
       {loginFail ? <ErrorHandler setLoginFail={setLoginFail} error={loginFail}/> :
    <> 
    <>
    {renderRedirect()}
    <h1 className='margenTop'>Registro</h1>
    <form onSubmit={registrar} onChange={handleChange}>
        <div>            
            <TextField
                type="text" name="email" defaultValue={""}
                sx={{ m: 1, width: '25ch' }}
                id="outlined-required"
                label="Email"
            />            
        </div>
        <div>
            <TextField
                type="text" name="password" defaultValue={""}
                id="outlined-required"
                label="Contraseña"                
            />            
        </div>
        <div>
            <TextField
                type="text" name="nombre" defaultValue={""}
                id="outlined-multiline-static"
                rows={4}
                label="Nombre"                
            />
        </div>
        <div>
            <TextField
                type="text" name="direccion" defaultValue={""}
                id="outlined-required"
                label="Direccion"                
            />
        </div>
        <div style={{display: "inline-block"}}>
        <PhoneInput
            placeholder="Telefono"
            name="telefono"
            value={value}
            onChange={setValue}
        />
        </div>
        <div>
            <TextField
                type="text" name="edad" defaultValue={""}
                id="outlined-required"
                label="Edad"                
            />
        </div>
        <div>
            <TextField
                type="text" name="avatar" defaultValue={""}
                id="outlined-required"
                label="Avatar"                
            />
        </div>
        <Button onClick={async (e) =>{
            await registrar(e)
            setRedirect(true)
        }} variant="contained" color="success">
            Registrar
        </Button>
        <br />
        <Link to={`/`}>
            <Button variant="contained" size="small">                            
                Home
            </Button>
        </Link> 
    </form>
    </>
    </>
    }</>
  )
}

export default Register