import {useState, createContext} from 'react'

import axios from 'axios'

export const CartContext =  createContext([])

export function CartContextProvider({children}) {

    const [carrito, setCarrito] = useState([])
    const [perfil, setPerfil] = useState("")
    
    const [token, setToken] = useState(null)
    const [idCarrito, setIdCarrito] = useState(null)
    const [prodsEnCarrito, setProdsEnCarrito] = useState([])


    const crearCarrito = () => { 
        axios({
            method: 'POST',
            url: `/carrito`,
            headers:{
                Authorization: token
            },
            data: {
                email: perfil.email,
                direccion: perfil.direccion
            }})
            .then(resp => resp.data)
            .then(data => {                
                setIdCarrito(data.id)
                setProdsEnCarrito([])
            })
    }

    const verCarrito = () => { 
        axios({
            method: 'GET',
            url: `/carrito/${idCarrito}/productos`,
            headers:{
                Authorization: token
            },
            })
            .then(response =>                
                response.data
             
            )
            .then(data => {
                if (!data.error) {                    
                    setProdsEnCarrito(data)
                }else
                    setProdsEnCarrito([])
                
            })
    }
    const agregarACarrito = (id) => {
        axios({
            method: 'POST',
            url: `/carrito/${idCarrito}/productos`,
            headers:{
                Authorization: token
            },
            data: {
                id: id
            }})
            .then(response => {                
                verCarrito()
            })
    }


    const itemsTotales = () => {
        
        return prodsEnCarrito.reduce( (total, item)=> total + item.cantidad, 0)        
    }    

    const eliminarItem = (id) => {
        axios({
            method: 'DELETE',
            url: `/carrito/${idCarrito}/productos/${id}`,
            headers:{
                Authorization: token
            },
            })
            .then(response => {                
                verCarrito()
            })
    }


    const eliminarCarrito = () => {        
        axios({
	
            method: 'DELETE',
            url: `/carrito/${idCarrito}`,
            headers:{
                Authorization: token
            },
            })
            .then(response => response.data)
            .then(data =>{
                if (data.msg) {                                        
                    crearCarrito()
                    verCarrito()
                }
            })
    }

    const generarOrden = () =>{
        axios({
	
            method: 'POST',
            url: `/carrito/${idCarrito}/finalizar`,
            headers:{
                Authorization: token
            },
            })
            .then(resp => resp.data)
            .then(data => {                
                crearCarrito()
                verCarrito()
            })
    }

    const getPerfil = (id) => {
        axios({	
            method: 'GET',
            url: `/perfil`,
            headers:{
                Authorization: id
            }
            })
            .then(resp => resp.data)
            .then(data => {                
                setPerfil(data)
            })
    }

    return (
        <CartContext.Provider value={{getPerfil, perfil, carrito, setCarrito, eliminarCarrito, eliminarItem, generarOrden, itemsTotales, crearCarrito, agregarACarrito, prodsEnCarrito, token, setToken }}>
            {children}
        </CartContext.Provider>
    )
}

export default CartContextProvider;